.contact{
    background: linear-gradient(to bottom, #242323, #1a1a33);
    padding-top: 2rem;
}
.con1{
    display: none;
}
.icon-contact{
    border-radius: 30%;
    width: 32px; /* Adjust the size as needed */
    height: 32px; /* Adjust the size as needed */
}
.contact__container{
    grid-template-columns: repeat(2,max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact__title{
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: 3rem;
    margin-bottom: var(--mb-1-5);
}

.contact__info{
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px;
}

.contact__card{
    background-color:linear-gradient(to bottom, #ea1313, #a50f0f);
    border: 1px solid rgb(130, 37, 237);
    padding: 1rem;
    border-radius: 1rem;
    text-align: center;
}

.contact__card-icon{
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-25);
}

.contact__card-title,
.contact__card-data{
    font-size: var(--small-font-size);
}

.contact__card-title{
    font-weight: var(--font-medium);
}

.contact__card-data{
    display: block;
    margin-bottom: var(--mb-0-75);
}

.contact__button{
    color:var(--text-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}

.contact__button-icon{
    font-size: 1rem;
    transition: 0.3s;
}

.contact__button:hover .contact__button-icon{
    transform: translate(0.25rem);
}

.contact_form{
    width: 360px;
}

.contact__form-div{
    position: relative;
    margin-bottom: var(--mb-2);
    height: 3rem;
}

.contact__form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 150%;
    height: 100%;
    border: 2px solid rgba(160, 24, 223, 0.3);
    background: none;
    color: var(--text-color);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
}

.section__subtitle{
  display: block;
  font-size: var(--small--font-size);
  margin-bottom: 3rem;
}
.contact__form-tag{
    position: absolute;
    top: -0.75rem;
    left: 1.2rem;
    font-weight: 700;
    font-size: var(--small-font-size);
    padding: 0.25rem;
    background-color: rgba(49, 44, 30, 0.51);
    z-index: 10;
}

.contact__form-area{
    height: 11rem;
}

.contact__form-are text{
    resize: none;
}

@media screen and (max-width: 992px) {
        .contact__container{
            column-gap: 3rem;
        }
       
  }

  @media screen and (max-width: 768px) {
    .contact__container{
        column-gap: 0rem;
    }
    .con{
        display: none;
    }
    .con1{
        display: block;
    }
    .contact__title{
        display: none;
       
    }
    .contact__container{
        grid-template-columns: 1fr;
        row-gap: 1.34rem;
    }
    .contact__form{
        margin: 0 auto;
    }
    .contact__form-input{
        width: 100%;
    }
    .contact__info{
        justify-content: space-evenly;
        display: grid;

    }
    .contact-email-title{
        font-size: 10px;
    }
    .contact-insta-title{
        font-size: 16px;
    }
    .contact__form-input {
        height: 100%;
    }
    .contact__card {
        flex:0 0;
    }
    .contact{
        padding-top: 0rem;
        padding-bottom: 15%;
    }
    .contact__info {
        display: flex;
    }
    .contact__form-div {
        box-sizing:border-box;
        margin-bottom: var(--mb-2);
    }
    
    .contact__card {
        flex: 1; /* Distribute space evenly among the cards */
    }
    .tex{
        font-size: 10px;
    }
    .cd{
        display: none;
    }
    .contact__card {
        border: 0px ;
        padding: 0rem;
    }
  
}

@media screen and (max-width: 511px) {
    .contact__info{
        grid-template-columns: 1fr;
    }
    .contact__form{
        width: 100%;
    }
  
}